package data

import dev.gitlive.firebase.firestore.DocumentSnapshot

data class Testimony(
    val ownerName: String,
    val email: String,
    val facebook: String,
    val image: String,
    val linkedin: String,
    val testimony: String,
    val title: String
) {
    companion object {
        fun fromDocument(myTestimoniesDocument: DocumentSnapshot): List<Testimony> {
            val myTestimonials = myTestimoniesDocument.get<Map<String, Map<String, String>>>("my_testomonials")
            return myTestimonials.keys.map { key ->
                Testimony(
                    ownerName = key,
                    email = myTestimonials[key]!!["email"]!!,
                    facebook = myTestimonials[key]!!["facebook"]!!,
                    image = myTestimonials[key]!!["image"]!!,
                    linkedin = myTestimonials[key]!!["linkedin"]!!,
                    testimony = myTestimonials[key]!!["testimony"]!!,
                    title = myTestimonials.get(key)!!["title"]!!
                )
            }
        }
    }
}

var testimonials = listOf<Testimony>()