package components

import csstype.*
import emotion.react.css
import kotlinx.js.jso
import properties.AboutMeWhoIamScreenProps
import react.FC
import react.dom.html.ReactHTML
import react.dom.html.ReactHTML.button
import react.dom.html.ReactHTML.div
import react.dom.html.ReactHTML.i
import react.dom.html.ReactHTML.img
import react.useState
import screens.SCREENS
import states.SideBarState

val AboutMeWhoIamSection = FC<AboutMeWhoIamScreenProps> { props ->
    var loadingImageState by useState<Boolean>(false)

    div {
        div {
            div {
                +props.myInfosAboutMe.name

                css {
                    color = NamedColor.black
                    fontSize = 40.px
                    fontWeight = FontWeight.bold
                    fontFamily = FontFamily.monospace
                    marginLeft = 40.px
                    marginBottom = 0.px
                    marginTop = 30.px
                }
            }

            div {
                +props.myInfosAboutMe.my_title

                css {
                    color = NamedColor.black
                    fontSize = 20.px
                    fontWeight = FontWeight.normal
                    fontFamily = FontFamily.monospace
                    marginLeft = 40.px
                    marginBottom = 20.px
                    marginTop = 30.px
                }
            }

            div {
                +props.myInfosAboutMe.whoIm

                css {
                    color = Color("#07268cad")
                    fontSize = 14.px
                    fontWeight = FontWeight.normal
                    fontFamily = FontFamily.monospace
                    marginLeft = 40.px
                    marginBottom = 20.px
                    width = if (props.positioning == SideBarState.SIDE) 40.vw else Auto.auto
                }
            }

            div {
                ReactHTML.button {
                    ReactHTML.i {
                        className = ClassName("fa-solid fa-circle-info")
                        style = jso {
                            fontSize = 16.px
                            color = NamedColor.white
                            marginRight = 5.px
                        }
                    }
                    +"View Portfolio"
                    onClick = {
                        props.updatescreenState(SCREENS.PORTFOLIO)
                    }
                    css {
                        marginRight = 10.px
                        borderRadius = 10.px
                        backgroundColor = Color("#54B689")
                        borderColor = Color("#54B689")
                        width = 150.px
                        height = 40.px
                        fontSize = 16.px
                        fontWeight = FontWeight.bold
                        color = NamedColor.white
                        hover {
                            backgroundColor = Color("#65bd95")
                            borderColor = Color("#65bd95")
                        }
                    }
                }

                button {
                    i {
                        className = ClassName("fa-solid fa-file")
                        style = jso {
                            fontSize = 16.px
                            color = NamedColor.white
                            marginRight = 5.px
                        }
                    }
                    +"View Resume"
                    onClick = {
                        props.updatescreenState(SCREENS.RESUME)
                    }
                    css {
                        marginRight = 10.px
                        borderRadius = 10.px
                        backgroundColor = Color("#4f4f4f")
                        borderColor = Color("#4f4f4f")
                        width = 150.px
                        height = 40.px
                        fontSize = 16.px
                        fontWeight = FontWeight.bold
                        color = NamedColor.white
                        hover {
                            backgroundColor = Color("#3f3f3f")
                            borderColor = Color("#3f3f3f")
                        }
                    }
                }
                css {
                    display = Display.flex
                    marginLeft = 40.px
                    marginBottom = 20.px
                }
            }


        }

        img {
            src = if(!loadingImageState) "images/shimmer.gif" else props.myInfosAboutMe.imageLink2
            onLoad = {
                loadingImageState = true
            }
            css {
                marginLeft = if (props.positioning == SideBarState.SIDE) 100.px else 40.px
                marginRight = 30.px
                marginTop = 40.px
                marginBottom = 40.px
                width = 300.px
                height = 300.px
                borderRadius = 10.px

            }
        }

        css {
            position = Position.sticky
            display = if (props.positioning == SideBarState.SIDE) Display.flex else Display.inlineBlock
            backgroundColor = Color("#c2c2c28a")
            width = if (props.positioning == SideBarState.SIDE) 100.vw - 300.px else 100.vw
        }
    }

}