package data

import dev.gitlive.firebase.firestore.DocumentSnapshot

data class Project(
    val name: String,
    val approachesAndSolutions: ApproachesAndSolutions,
    val client: Client,
    val image: Image,
    val projectChallanges: ProjectChallanges,
    val projectOverview: ProjectOverview,
    val projectRequirement: ProjectRequirement,
    val result: Result,
    val teamMembers: List<TeamMember>
) {
    companion object {
        fun fromDocument(myProjectsDoc: DocumentSnapshot): List<Project> {
            val myProjects = myProjectsDoc.get<Map<String, Map<String, Map<String, String>>>>("my_projects")

            val projectResults = myProjectsDoc.get<Map<String, Map<String, Map<String, String>>>>("results")

            val teamMembersMap = myProjectsDoc.get<Map<String, Map<String, Map<String, String>>>>("team_members")
            return myProjects.keys.map { projectName ->
                Project(
                    name = projectName,
                    approachesAndSolutions = ApproachesAndSolutions(
                        description = myProjects[projectName]!!["approaches_and_solutions"]!!["description"]!!,
                        imageOrVedio = myProjects[projectName]!!["approaches_and_solutions"]!!["image_or_vedio"]!!,
                        link = myProjects[projectName]!!["approaches_and_solutions"]!!["link"]!!
                    ),
                    client = Client(
                        description = myProjects[projectName]!!["client"]!!["description"]!!,
                        industry = myProjects[projectName]!!["client"]!!["industry"]!!,
                        name = myProjects[projectName]!!["client"]!!["name"]!!,
                        size = myProjects[projectName]!!["client"]!!["size"]!!,
                        website = myProjects[projectName]!!["client"]!!["website"]!!
                    ),
                    image = Image(link = myProjects[projectName]!!["image"]!!["link"]!!),
                    projectChallanges = ProjectChallanges(
                        info = myProjects[projectName]!!["project_challanges"]!!["info"]!!,
                    ),
                    projectOverview = ProjectOverview(
                        info = myProjects[projectName]!!["project_overview"]!!["info"]!!,
                        type = myProjects[projectName]!!["project_overview"]!!["type"]!!,
                        headline = myProjects[projectName]!!["project_overview"]!!["headline"]!!,
                        intro = myProjects[projectName]!!["project_overview"]!!["intro"]!!,
                    ),
                    projectRequirement = ProjectRequirement(info = myProjects[projectName]!!["project_requirement"]!!["info"]!!),
                    result = Result(
                        customerSatisfaction = CustomerSatisfaction(
                            description = projectResults[projectName]!!["Customer satisfaction"]!!["description"]!!,
                            percent = projectResults[projectName]!!["Customer satisfaction"]!!["percent"]!!,
                            upDown = projectResults[projectName]!!["Customer satisfaction"]!!["up_down"]!!
                        ),
                        overallCost = OverallCost(
                            description = projectResults[projectName]!!["Overall Cost"]!!["description"]!!,
                            percent = projectResults[projectName]!!["Overall Cost"]!!["percent"]!!,
                            upDown = projectResults[projectName]!!["Overall Cost"]!!["up_down"]!!
                        ),
                        efficiency = Efficiency(
                            description = projectResults[projectName]!!["Efficiency"]!!["description"]!!,
                            percent = projectResults[projectName]!!["Efficiency"]!!["percent"]!!,
                            upDown = projectResults[projectName]!!["Efficiency"]!!["up_down"]!!
                        )
                    ),
                    teamMembers = teamMembersMap[projectName]!!.keys.map { teamMemberName ->
                        TeamMember(
                            name = teamMemberName,
                            email = teamMembersMap[projectName]!![teamMemberName]!!["email"]!!,
                            image = teamMembersMap[projectName]!![teamMemberName]!!["image"]!!,
                            title = teamMembersMap[projectName]!![teamMemberName]!!["title"]!!
                        )
                    }.toList()
                )
            }
        }
    }
}

data class ApproachesAndSolutions(val description: String, val imageOrVedio: String, val link: String)

data class Client(
    val description: String,
    val industry: String,
    val name: String,
    val size: String,
    val website: String
)

data class Image(val link: String)

data class ProjectChallanges(val info: String)

data class ProjectOverview(val info: String, val type: String, val headline: String, val intro: String)

data class ProjectRequirement(val info: String)

data class CustomerSatisfaction(val description: String, val percent: String, val upDown: String)

data class OverallCost(val description: String, val percent: String, val upDown: String)

data class Efficiency(val description: String, val percent: String, val upDown: String)

data class Result(
    val customerSatisfaction: CustomerSatisfaction,
    val overallCost: OverallCost,
    val efficiency: Efficiency
)

data class TeamMember(val name: String, val email: String, val image: String, val title: String)

var projectList = listOf<Project>()


