package components

import csstype.*
import emotion.react.css
import kotlinx.js.jso
import properties.ClientLineProps
import react.FC
import react.dom.html.ReactHTML

val ClientLine = FC<ClientLineProps> { props ->

    ReactHTML.div {
        ReactHTML.i {
            className = ClassName(props.icon)
            style = jso {
                fontSize = 20.px
                color = Color("#4f4f4f")
                marginRight = 5.px
            }
        }
        ReactHTML.div {
            +props.title
            css {
                fontSize = 16.px
                fontWeight = FontWeight.bold
                fontFamily = FontFamily.monospace
                color = Color("#4f4f4f")
            }
        }

        if(props.linkOrInfo) {
            ReactHTML.a {
                +props.infoOrLink
                href = props.infoOrLink
                css {
                    fontSize = 14.px
                    fontWeight = FontWeight.normal
                    fontFamily = FontFamily.monospace
                    color = Color("#4f4f4f")
                }
            }
        } else {
            ReactHTML.div {
                +props.infoOrLink
                css {
                    fontSize = 14.px
                    fontWeight = FontWeight.normal
                    fontFamily = FontFamily.monospace
                    color = Color("#4f4f4f")
                }
            }
        }

        css {
            display = Display.flex
            flexDirection = FlexDirection.row
            marginBottom = 10.px
        }
    }
}