package states

import models.MyInfos
import react.Props
import screens.SCREENS

enum class SideBarState {
    SIDE,
    TOP
}
external interface RelativeSideBarState : Props {
    var positioning: SideBarState
    var myInfos: MyInfos
    var currentScreenState: SCREENS
    var updatescreenState: (SCREENS) -> Unit
    var showRelativeState: Boolean
    var updateShowRelativeState: () -> Unit
}