package components

import csstype.*
import emotion.react.css
import properties.AboutMeWhatIDoProps
import react.FC
import react.dom.html.ReactHTML.div
import react.dom.html.ReactHTML.em
import screens.SCREENS

val AboutMeWhatIDo = FC<AboutMeWhatIDoProps> { props ->
    div {
        div {
            div {

                css {
                    backgroundColor = Color("#54B689")
                    marginRight = 5.px
                    width = 10.px
                }
            }
            +"What I do"

            css {
                fontSize = 30.px
                fontFamily = FontFamily.monospace
                fontWeight = FontWeight.bold
                marginBottom = 10.px
                display = Display.flex
            }
        }

        div {
            +props.whatIDo.get("info")!!

            em {
                +"project portfolio"

                onClick = {
                    props.updatescreenState(SCREENS.PORTFOLIO)
                }
                css {
                    color = Color("#07268cad")
                    cursor = Cursor.pointer
                    fontSize = 14.px
                    fontFamily = FontFamily.monospace
                    fontWeight = FontWeight.normal
                    hover {
                        color = Color("#07268c")
                        textDecoration = TextDecoration.underline
                    }
                }
            }

            em {
                +" or "

                css {
                    fontSize = 14.px
                    fontFamily = FontFamily.monospace
                    fontWeight = FontWeight.normal
                }
            }

            em {
                +"my resume"

                onClick = {
                    props.updatescreenState(SCREENS.RESUME)
                }
                css {
                    color = Color("#07268cad")
                    cursor = Cursor.pointer
                    fontSize = 14.px
                    fontFamily = FontFamily.monospace
                    fontWeight = FontWeight.normal
                    hover {
                        color = Color("#07268c")
                        textDecoration = TextDecoration.underline
                    }
                }
            }
            css {
                fontSize = 14.px
                fontFamily = FontFamily.monospace
                fontWeight = FontWeight.normal
            }
        }
        css {
            marginTop = 20.px
            marginLeft = 40.px
            marginBottom = 40.px
        }
    }
}