package models

import dev.gitlive.firebase.firestore.DocumentSnapshot
import kotlinx.serialization.Serializable

@Serializable
data class MyInfos(
    val name: String,
    val phoneNumber: String,
    val email: String,
    val linkedin: String,
    val github: String,
    val address: String,
    val hi: String,
    val whoIm: String,
    val imageLink: String,
    val imageLink2: String,
    val whatCanIDo: Map<String, String>,
    val my_title: String
) {
    fun getName(): String {
        return this.name
    }
    companion object {
        fun fromDocument(documentSnapshot: DocumentSnapshot): MyInfos {
            return MyInfos(
                name = documentSnapshot.get<String>("name") ?: "",
                phoneNumber = documentSnapshot.get<String>("phone_number")?: "",
                email = documentSnapshot.get<String>("email")?: "",
                linkedin = documentSnapshot.get<String>("linkedin")?: "",
                github = documentSnapshot.get<String>("github")?: "",
                address = documentSnapshot.get<String>("address")?: "",
                hi = documentSnapshot.get<String>("hi")?: "",
                whoIm = documentSnapshot.get<String>("who_i_am")?: "",
                whatCanIDo = documentSnapshot.get<Map<String, String>>("what_can_i_do")?: mapOf(),
                imageLink = documentSnapshot.get<String>("imageLink")?: "",
                my_title = documentSnapshot.get<String>("my_title")?: "",
                imageLink2 = documentSnapshot.get<String>("imageLink2")?: "",
            )
        }
    }

}


