package screens

import components.ExperienceCard
import components.ProjectCard
import csstype.*
import emotion.react.css
import properties.PortfolioScreenProps
import react.FC
import react.dom.html.ReactHTML.div
import states.SideBarState

val PortfolioScreen = FC<PortfolioScreenProps> { props ->
    var numColumns= if(props.positioning == SideBarState.SIDE) 2 else 1


    div {

        div {
            +"Experience"

            css {
                marginLeft = 20.px
                marginTop  = 30.px
                marginBottom = 30.px
                fontFamily = FontFamily.monospace
                fontSize = 30.px
                fontWeight = FontWeight.bold
            }
        }

        div {
            props.myExperience.forEach { expr ->
                ExperienceCard {
                    positioning = props.positioning
                    experience = expr
                }
            }
            css {
                marginLeft = 20.px
                marginTop  = 10.px
                display = Display.flex
                flexDirection = FlexDirection.column
            }
        }


        div {
            +"Projects"

            css {
                marginLeft = 20.px
                marginTop  = 30.px
                marginBottom = 30.px
                fontFamily = FontFamily.monospace
                fontSize = 30.px
                fontWeight = FontWeight.bold
            }
        }
        val numRows = if(props.myProjects.size % numColumns == 0) props.myProjects.size / numColumns else props.myProjects.size / numColumns + 1
        div {
            for(j in 0..< numRows) {
                div {
                    val currentColumns = if(j == numRows - 1) if(props.myProjects.size % numColumns == 0) numColumns else numColumns - 1 else numColumns
                    for(i in 0..< currentColumns) {
                        ProjectCard {
                            projectName = props.myProjects[i + numColumns * j].name
                            projectHeadline = props.myProjects[i + numColumns * j].projectOverview.headline
                            projectClient = props.myProjects[i + numColumns * j].client.name
                            projectImageLink = props.myProjects[i + numColumns * j].image.link
                            positioning = props.positioning
                            chooseProjectFunction = props.chooseProjectFunction
                        }
                    }
                    css {
                        display = Display.flex
                        flexDirection = FlexDirection.row
                    }
                }
            }

            css {
                marginTop = 20.px
                marginLeft = 40.px
            }
        }

        css {
            display = Display.flex
            flexDirection = FlexDirection.column
            overflowY = Overflow.scroll
            width = if (props.positioning == SideBarState.SIDE) 100.vw - 300.px else 100.vw
        }
    }
}