package components

import csstype.*
import emotion.react.css
import properties.ProjectCardProps
import react.FC
import react.dom.html.ReactHTML
import react.dom.html.ReactHTML.button
import react.dom.html.ReactHTML.div
import react.useState
import states.SideBarState

val ProjectCard = FC<ProjectCardProps> { props ->
    var loadingImageState by useState<Boolean>(false)

    div {

        ReactHTML.img {
            src = if (!loadingImageState) "images/shimmer.gif" else props.projectImageLink
            onLoad = {
                loadingImageState = true
                println("Image Loaded")
            }
            css {
                width = 200.px
                height = 200.px

            }
        }

        div {
            div {
                +props.projectName

                css {
                    fontSize = 18.px
                    fontWeight = FontWeight.bold
                    fontFamily = FontFamily.monospace
                    color = NamedColor.black
                    marginTop = 5.px
                    marginLeft = 20.px
                    marginBottom = 10.px
                    height = 20.pct
                }
            }

            div {
                +props.projectHeadline

                css {
                    fontSize = 14.px
                    marginRight = 5.px
                    fontWeight = FontWeight.normal
                    fontFamily = FontFamily.monospace
                    textAlign = TextAlign.justify
                    color = NamedColor.gray
                    marginLeft = 20.px
                    marginBottom = 10.px
                    height = 60.pct
                    overflowY = Overflow.scroll
                    scrollbarWidth = None.none
                }
            }

            div {
                +"client: ${props.projectClient}"

                css {
                    fontSize = 12.px
                    fontWeight = FontWeight.normal
                    fontFamily = FontFamily.monospace
                    color = NamedColor.gray
                    marginLeft = 20.px
                    height = 20.pct
                }
            }
            css {
                display = Display.flex
                flexDirection = FlexDirection.column
            }
        }

        button {
            onClick = {
                props.chooseProjectFunction(props.projectName)
            }
            css {
                position = Position.absolute
                alignItems = AlignItems.center
                justifyContent = JustifyContent.center
                top = 0.px
                left = 0.px
                marginBottom = 20.px
                width = 100.pct
                height = 200.px
                backgroundColor = NamedColor.transparent
                border = None.none
                hover {
                    backgroundColor = rgba(169,169,169,0.5)
                }
            }
        }

        css {
            position = Position.relative
            display = Display.flex
            flexDirection = FlexDirection.row
            marginBottom = 20.px
            width = if (props.positioning == SideBarState.SIDE) 45.pct else 100.pct
            height = 200.px
            marginRight = 20.px
            backgroundColor = Color("#fafafa")

        }
    }



}