package screens

import components.ContactBar
import csstype.*
import emotion.react.css
import properties.ContactScreenProps
import react.FC
import react.dom.html.ReactHTML
import react.dom.html.ReactHTML.a
import react.dom.html.ReactHTML.div
import states.SideBarState

val ContactScreen = FC<ContactScreenProps> { props ->
    div {
        div {
            ReactHTML.div {
                +"Contact"
                css {
                    fontSize = 30.px
                    fontWeight = FontWeight.bold
                    fontFamily = FontFamily.monospace
                    width = 100.pct
                    marginTop = 40.px
                    textAlign = TextAlign.center
                }
            }
            ReactHTML.div {
                +"Interested in hiring me for your project or just want to say hi? You can fill in the contact form below or send me an email to "
                a {
                    +props.myInfosAboutMe.email
                    href = "mailto: ${props.myInfosAboutMe.email}"

                    css {
                        color = Color("#07268cad")
                        cursor = Cursor.pointer
                        fontSize = 14.px
                        fontFamily = FontFamily.monospace
                        fontWeight = FontWeight.normal
                        hover {
                            color = Color("#07268c")
                            textDecoration = TextDecoration.underline
                        }
                    }
                }
                css {
                    fontSize = 16.px
                    fontWeight = FontWeight.normal
                    fontFamily = FontFamily.monospace
                    marginRight = 20.pct
                    marginLeft = 20.pct
                    marginTop = 20.px
                    textAlign = TextAlign.justify
                    overflowY = Overflow.scroll
                    scrollbarWidth = None.none
                }
            }

            ReactHTML.div {
                +"Want to get connected? Follow me on the social channels below."

                css {
                    fontSize = 16.px
                    fontWeight = FontWeight.normal
                    fontFamily = FontFamily.monospace
                    marginRight = 20.pct
                    marginLeft = 20.pct
                    marginTop = 20.px
                    textAlign = TextAlign.justify
                    overflowY = Overflow.scroll
                    scrollbarWidth = None.none
                    marginBottom = 100.px
                }
            }

            ContactBar {

            }
            css {
                backgroundColor = Color("#c7d1f4")
                height = 40.vh
            }
        }


        css {
            display = Display.flex
            flexDirection = FlexDirection.column
            position = Position.sticky
            overflowY = Overflow.scroll
            width = if(props.positioning == SideBarState.SIDE) (100.vw - 300.px) else 100.vw
            marginTop = if(props.positioning == SideBarState.SIDE) (30.vh) else 0.vw

        }
    }

}