package components

import csstype.Display
import csstype.FlexDirection
import csstype.px
import emotion.react.css
import properties.SkillGridProperties
import react.FC
import react.dom.html.ReactHTML.div
import states.SideBarState

val SkillGrid = FC<SkillGridProperties> { props ->

    var numColumns= if(props.positioning == SideBarState.SIDE) props.numColumns else 1

    println("numColumns = $numColumns")
    val numRows = props.skills.size / numColumns
    div {
        for(j in 0..< numRows) {
            div {

                for(i in 0..< numColumns) {
                    SkillCard {
                        title = props.skills[i + numColumns * j].title
                        icon  = props.skills[i + numColumns * j].icons
                        info  = props.skills[i + numColumns * j].info
                        positioning = props.positioning
                        rightMargin = 40.px
                        bottomMargin = 20.px
                    }
                }
                css {
                    display = Display.flex
                    flexDirection = FlexDirection.row
                }
            }
        }

        css {
            marginTop = 20.px
            marginLeft = 40.px
        }
    }

}