package components

import csstype.*
import emotion.react.css
import kotlinx.js.jso
import properties.ExperienceCardProperties
import react.FC
import react.dom.html.ReactHTML.a
import react.dom.html.ReactHTML.div
import react.dom.html.ReactHTML.i
import react.dom.html.ReactHTML.img
import react.dom.html.ReactHTML.p
import react.useState

val ExperienceCard = FC<ExperienceCardProperties> { props ->
    var loadingImageState by useState<Boolean>(false)

    div {
        div {

            img {
                src = if(!loadingImageState) "images/shimmer.gif" else props.experience.image
                onLoad = {
                    loadingImageState = true
                    println("Image Loaded")
                }
                css {
                    width = 100.px
                    height = 100.px
                    borderRadius = 50.pct
                }
            }

            div {
                div {
                    +props.experience.title
                    css {
                        fontSize = 18.px
                        fontWeight = FontWeight.bold
                        fontFamily = FontFamily.monospace
                        color = Color("#07268cad")
                    }
                }

                div {
                    a {
                        href = props.experience.link
                        +props.experience.company
                        css {
                            fontSize = 14.px
                            fontWeight = FontWeight.bold
                            fontFamily = FontFamily.monospace
                            fontStyle = FontStyle.normal
                            textDecoration = None.none
                            color = NamedColor.gray
                        }
                    }

                    div {
                        css {
                            marginLeft = 10.px
                            marginRight = 10.px
                            fontSize = 14.px
                            fontWeight = FontWeight.bold
                            fontFamily = FontFamily.monospace
                            fontStyle = FontStyle.normal
                            color = NamedColor.black
                        }
                    }

                    div {
                        +props.experience.period
                        css {
                            fontSize = 14.px
                            fontWeight = FontWeight.bold
                            fontFamily = FontFamily.monospace
                            fontStyle = FontStyle.normal
                            color = NamedColor.black
                        }
                    }
                    css {
                        marginTop = 10.px
                        display = Display.flex
                        flexDirection = FlexDirection.row
                    }
                }
                a {
                    href = "http://maps.google.com/?q=${props.experience.address}"
                    i {
                        className = ClassName("fa-solid fa-location-dot")
                        style = jso {
                            fontSize = 14.px
                            color = Color("#07268cad")
                            textAlign = TextAlign.center
                            position = Position.relative
                            marginRight = 10.px
                            marginRight = 10.px
                        }

                    }
                    +props.experience.address

                    css {
                        fontSize = 14.px
                        marginTop = 10.px
                        fontWeight = FontWeight.bold
                        fontFamily = FontFamily.monospace
                        fontStyle = FontStyle.normal
                        textDecoration = None.none
                        color = Color("#54B689")
                    }
                }


                css {
                    marginLeft = 10.px
                    display = Display.flex
                    flexDirection = FlexDirection.column
                    justifyContent = JustifyContent.center
                }
            }

            css {
                display = Display.flex
                flexDirection = FlexDirection.row
            }
        }

        div {
            +props.experience.companyDescription
            css {
                marginLeft = 10.px
                marginTop = 10.px
                fontSize = 12.px
                fontWeight = FontWeight.bold
                fontFamily = FontFamily.monospace
                fontStyle = FontStyle.normal
                color = NamedColor.black
            }
        }
        div {
            +"Responsibility:"
            css {
                marginLeft = 10.px
                marginTop = 10.px
                fontSize = 14.px
                fontWeight = FontWeight.bold
                fontFamily = FontFamily.monospace
                fontStyle = FontStyle.normal
                color = Color("#07268cad")
            }
        }

        div {
            props.experience.responsibility.split("##").forEach {
                p {
                    +it
                    css {
                        fontSize = 14.px
                        fontWeight = FontWeight.normal
                        fontFamily = FontFamily.monospace
                        fontStyle = FontStyle.normal
                        color = NamedColor.black
                    }
                }
            }
            css {
                marginLeft = 10.px
            }
        }

        css {
            display = Display.flex
            flexDirection = FlexDirection.column
            marginBottom = 20.px
        }
    }
}