package data

data class Skill(val title: String, val info: String, val icons: List<String>) {
    companion object {
        fun fromDocument(skills: Map<String, Map<String, String>>): List<Skill> {
            return skills.keys.map {   key ->

                val iconList = skills.get(key)!!.get("icons")!!.split("##")
                val info = skills.get(key)!!.get("Info")!!
                Skill(title = key, info =  info,  icons = iconList)
            }

        }
    }
}