package components

import csstype.*
import emotion.react.css
import properties.AboutMeTestimonialsProps
import react.FC
import react.dom.html.ReactHTML.div

val AboutMeTestimonials = FC<AboutMeTestimonialsProps> { props ->

    div {
        div {
            div {

                css {
                    backgroundColor = Color("#54B689")
                    marginRight = 5.px
                    width = 10.px
                }
            }
            +"Testimonials"

            css {
                fontSize = 30.px
                fontFamily = FontFamily.monospace
                fontWeight = FontWeight.bold
                marginBottom = 10.px
                display = Display.flex
            }
        }

        props.abutMeTestimonials.forEach { testimony ->
            TestimonyCard {
                positioning = props.positioning
                testimpnyData = testimony
            }
        }


        css {
            marginLeft = 40.px
        }
    }
}