
import components.ResponsiveSideBar
import csstype.*
import data.*
import dev.gitlive.firebase.Firebase
import dev.gitlive.firebase.FirebaseOptions
import dev.gitlive.firebase.firestore.firestore
import dev.gitlive.firebase.initialize
import emotion.react.css
import kotlinx.browser.window
import kotlinx.coroutines.GlobalScope
import kotlinx.coroutines.promise
import kotlinx.js.jso
import models.MyInfos
import react.FC
import react.Props
import react.dom.html.ReactHTML
import react.dom.html.ReactHTML.div
import react.dom.html.ReactHTML.img
import react.useState
import screens.*
import states.SideBarState

val App = FC<Props> { appProps ->
    var loadingImageLink = ""
    var clicked by useState<Boolean>(false)
    var sidebarstate by useState<SideBarState>(
        if (window.innerWidth < 700) {
            SideBarState.TOP
        } else {
            SideBarState.SIDE
        }
    )
    var currentScreen by useState<SCREENS>(SCREENS.ABOUT_ME)
    var currentProject by useState<String>("")

    var showRelative by useState<Boolean>(false)
    var numOfSkillsColumnState by useState<Int>((window.innerWidth - 300) / 350)

    val updateShowRelative = {
        showRelative = !showRelative
    }

    val updateScreen = { newScreen: SCREENS ->
        println(newScreen)
        currentScreen = newScreen

    }

    val chooseProject = { project: String ->
        currentProject = project
        currentScreen = SCREENS.PROJECT
    }

    var getData by useState<Boolean>(false)
    GlobalScope.promise {
        // Your Firestore code here
        val config = FirebaseOptions(
            apiKey = "AIzaSyDGKXVWEDJxdK-EItY1WLOQzdWZeeC20jY",
            authDomain = "portfolio-fa257.firebaseapp.com",
            projectId = "portfolio-fa257",
            storageBucket = "portfolio-fa257.appspot.com",
            applicationId = "1:171419052437:web:f0142807dd59a7ce58ee9c",
            gcmSenderId = "171419052437",
            gaTrackingId = "G-88L54MPRG1"
        )
        Firebase.initialize(options = config)

        val collection = Firebase.firestore.collection("about_me")
        val projectsCollection = Firebase.firestore.collection("projects")

        val myDoc = collection.document("my_infos").get()

        val mySkills = collection.document("my_skills").get()

        val myTestimonies = collection.document("testimonials").get()

        val myExperienceDoc = projectsCollection.document("experience").get()

        val myProjectsDoc = projectsCollection.document("projects").get()

        if (myDoc.exists) {
            println("my_infos exists")
            myPortfolioInfos = MyInfos.fromDocument(myDoc)
        } else {
            println("my_infos not exists")
        }

        if (mySkills.exists) {
            println("my_skills exists")
            mySkillsSet = Skill.fromDocument(mySkills.get<Map<String, Map<String, String>>>("skills"))
        } else {
            println("my_skills not exists")
        }

        if (myTestimonies.exists) {
            println("myTestimonies exists")
            testimonials = Testimony.fromDocument(myTestimonies)
        } else {
            println("myTestimonies not exists")
        }

        if (myExperienceDoc.exists) {
            println("myExperienceDoc exists")
            experienceList = Experience.fromDocument(myExperienceDoc)
        } else {
            println("myExperienceDoc not exists")
        }

        if (myProjectsDoc.exists) {
            println("myProjectsDoc exists")
            projectList = Project.fromDocument(myProjectsDoc)
            println("my projects = ${projectList}")
        } else {
            println("myExperienceDoc not exists")
        }



        getData = true
    }

    window.onresize = {
        val viewWidth = window.innerWidth
        numOfSkillsColumnState = (viewWidth - 300) / 350
        if (viewWidth < 700) {
            sidebarstate = SideBarState.TOP
        } else {
            sidebarstate = SideBarState.SIDE
        }
    }

    println(getData)
    if (!getData) {
        div {
            img {
                src = "https://mir-s3-cdn-cf.behance.net/project_modules/max_632/04de2e31234507.564a1d23645bf.gif"
            }

            css {
                display = Display.flex
                width = 100.vw
                height = 100.vh
                justifyContent = JustifyContent.center
                alignItems = AlignItems.center
            }
        }
    } else {
        println("My Infos")
        println(myPortfolioInfos)
        div {
            ResponsiveSideBar {
                positioning = sidebarstate
                myInfos = myPortfolioInfos
                currentScreenState = currentScreen
                updatescreenState = updateScreen
                updateShowRelativeState = updateShowRelative
                showRelativeState = showRelative


            }

            div {

                if (currentScreen == SCREENS.ABOUT_ME) {
                    AboutMeScreen {
                        positioning = sidebarstate
                        myInfosAboutMe = myPortfolioInfos
                        mySkillsAboutMe = mySkillsSet
                        updatescreenState = updateScreen
                        myTestimonialsAboutMe = testimonials
                        numOfColumns = numOfSkillsColumnState
                    }
                } else if (currentScreen == SCREENS.PORTFOLIO) {
                    PortfolioScreen {
                        positioning = sidebarstate
                        myExperience = experienceList
                        myProjects = projectList
                        updatescreenState = updateScreen
                        chooseProjectFunction = chooseProject
                    }

                } else if (currentScreen == SCREENS.PROJECT) {
                    PojectScreen {
                        project = projectList.find { it.name == currentProject }!!
                        positioning = sidebarstate
                    }

                } else if (currentScreen == SCREENS.RESUME) {
                    ReactHTML.embed {
                        src = "pdfs/GhaithAbuHussainResume.pdf"
                        style = jso {
                            width = if (sidebarstate == SideBarState.SIDE) (100.vw - 300.px) else 100.vw
                            height = if (sidebarstate == SideBarState.SIDE) 100.vh else 90.vh
                        }
                        type = "application/pdf"
                    }

                } else if (currentScreen == SCREENS.CONTACT) {
                    ContactScreen {
                        positioning = sidebarstate
                        myInfosAboutMe = myPortfolioInfos
                    }

                } else {
                    +currentScreen.name
                }

                css {
                    position = Position.fixed
                    display = Display.grid
                    height = 100.vh
                    left = 0.px
                    top = 0.px
                    if (sidebarstate == SideBarState.SIDE) {
                        marginLeft = 300.px
                        marginTop = 0.vh
                    } else {
                        marginLeft = 0.px
                        marginTop = if (showRelative) 50.vh else 10.vh
                    }
                }
            }




            css {
                display = if (sidebarstate == SideBarState.SIDE) Display.flex else Display.block
            }
        }
    }


}