package screens

import ReactPlayer
import components.ClientLine
import csstype.*
import emotion.react.css
import properties.ProjectScreenProps
import react.FC
import react.dom.html.ReactHTML
import react.dom.html.ReactHTML.div
import react.dom.html.ReactHTML.img
import react.useState
import states.SideBarState

val PojectScreen = FC<ProjectScreenProps> { props ->
    var loadingProjectImageState by useState<Boolean>(false)

    div {

        div {
            div {
                +props.project.name
                css {
                    fontSize = 30.px
                    fontWeight = FontWeight.bold
                    fontFamily = FontFamily.monospace
                    width = 100.pct
                    marginTop = 40.px
                    textAlign = TextAlign.center
                }
            }
            div {
                +props.project.projectOverview.intro
                css {
                    fontSize = 16.px
                    fontWeight = FontWeight.normal
                    fontFamily = FontFamily.monospace
                    marginRight = 20.pct
                    marginLeft = 20.pct
                    marginTop = 20.px
                    textAlign = TextAlign.justify
                    overflowY = Overflow.scroll
                    scrollbarWidth = None.none
                }
            }
            css {
                display = Display.flex
                flexDirection = FlexDirection.column
                fontSize = 30.px
                fontWeight = FontWeight.bold
                fontFamily = FontFamily.monospace
                width = 100.pct
                marginTop = 40.px
                textAlign = TextAlign.center
                backgroundColor = Color("#fafafa")
            }
        }

        div {
            if(props.positioning == SideBarState.SIDE) {
                img {
                    src = if (!loadingProjectImageState) "images/shimmer.gif" else props.project.image.link
                    onLoad = {
                        loadingProjectImageState = true
                    }
                    css {
                        width = 200.px
                        height = 300.px
                        marginTop = 50.px
                        marginLeft = 30.px
                    }
                }
            }

            div {
                div {
                    +props.project.client.name
                    css {
                        fontSize = 30.px
                        fontWeight = FontWeight.bold
                        fontFamily = FontFamily.monospace
                        marginBottom = 20.px
                        textAlign = TextAlign.start
                    }
                }

                ClientLine {
                    icon = "fa-solid fa-industry"
                    title = "Industry: "
                    linkOrInfo = false
                    infoOrLink = props.project.client.industry
                }

                ClientLine {
                    icon = "fa-solid fa-users"
                    title = "Size: "
                    linkOrInfo = false
                    infoOrLink = props.project.client.size
                }

                ClientLine {
                    icon = "fa-solid fa-link"
                    title = "Website: "
                    linkOrInfo = true
                    infoOrLink = props.project.client.website
                }

                div {
                    +props.project.client.description
                    css {
                        fontSize = 14.px
                        fontWeight = FontWeight.normal
                        fontFamily = FontFamily.monospace
                        marginTop = 10.px
                        marginBottom = 20.px
                        textAlign = TextAlign.justify
                        marginRight = 5.px
                    }
                }

                div {
                    +"Project Requirements: "
                    css {
                        fontSize = 16.px
                        fontWeight = FontWeight.bold
                        fontFamily = FontFamily.monospace
                        marginBottom = 10.px
                        textAlign = TextAlign.justify
                    }
                }

                div {
                    props.project.projectRequirement.info.split("##").forEach {
                        ReactHTML.p {
                            +it
                            css {
                                fontSize = 14.px
                                fontWeight = FontWeight.normal
                                fontFamily = FontFamily.monospace
                                fontStyle = FontStyle.normal
                                color = NamedColor.black
                            }
                        }
                    }
                    css {
                        marginLeft = 20.px
                        textAlign = TextAlign.justify
                        marginRight = 5.px
                    }
                }

                css {
                    display = Display.flex
                    flexDirection = FlexDirection.column
                    marginLeft = 30.px
                    marginTop = 50.px
                }
            }
            css {
                display = Display.flex
                flexDirection = FlexDirection.row
                width = 80.pct
                marginTop = 20.px
                marginLeft = 10.pct
                textAlign = TextAlign.center
                backgroundColor = Color("#fafafa")
            }
        }

        div {
            +"Project Overview"
            css {
                fontSize = 25.px
                fontWeight = FontWeight.bold
                fontFamily = FontFamily.monospace
                width = 80.pct
                marginTop = 40.px
                marginBottom = 20.px
                marginLeft = 10.pct
                textAlign = TextAlign.start
            }
        }

        div {
            +props.project.projectOverview.info
            css {
                fontSize = 14.px
                fontWeight = FontWeight.normal
                fontFamily = FontFamily.monospace
                width = 80.pct
                marginLeft = 10.pct
                textAlign = TextAlign.start
            }
        }

        div {
            +"The Challenge"
            css {
                fontSize = 25.px
                fontWeight = FontWeight.bold
                fontFamily = FontFamily.monospace
                width = 80.pct
                marginTop = 40.px
                marginBottom = 20.px
                marginLeft = 10.pct
                textAlign = TextAlign.start
            }
        }

        div {
            +props.project.projectChallanges.info
            css {
                fontSize = 14.px
                fontWeight = FontWeight.normal
                fontFamily = FontFamily.monospace
                width = 80.pct
                marginLeft = 10.pct
                textAlign = TextAlign.start
            }
        }

        div {
            +"The Approach & Solution"
            css {
                fontSize = 25.px
                fontWeight = FontWeight.bold
                fontFamily = FontFamily.monospace
                width = 80.pct
                marginTop = 40.px
                marginBottom = 20.px
                marginLeft = 10.pct
                textAlign = TextAlign.start
            }
        }

        div {
            +props.project.approachesAndSolutions.description
            css {
                fontSize = 14.px
                fontWeight = FontWeight.normal
                fontFamily = FontFamily.monospace
                width = 80.pct
                marginLeft = 10.pct
                textAlign = TextAlign.start
            }
        }

        div {
            if(props.project.approachesAndSolutions.imageOrVedio == "image") {
                img {
                    src = if (!loadingProjectImageState) "images/shimmer.gif" else props.project.approachesAndSolutions.link
                    onLoad = {
                        loadingProjectImageState = true
                    }
                    css {
                        width = 800.px
                        height = 800.px
                        justifyContent = JustifyContent.center
                        textAlign = TextAlign.center
                        alignItems = AlignItems.center
                        marginLeft = if(props.positioning == SideBarState.TOP) 0.px else (50.pct - 400.px)
                    }
                }
            } else {
                div {
                    ReactPlayer {
                        url = props.project.approachesAndSolutions.link
                        controls = true
                    }

                    css {
                        marginLeft = if(props.positioning == SideBarState.TOP) 0.px else 30.pct
                    }
                }

            }

            css {
                marginBottom = 100.px
                marginTop = 40.px
            }
        }

        css {
            display = Display.flex
            flexDirection = FlexDirection.column
            overflowY = Overflow.scroll
            width = if (props.positioning == SideBarState.SIDE) 100.vw - 300.px else 100.vw
        }
    }
}