package screens

import components.AboutMeTestimonials
import components.AboutMeWhatIDo
import components.AboutMeWhoIamSection
import components.SkillGrid
import csstype.Display
import csstype.FlexDirection
import csstype.Overflow
import csstype.Position
import emotion.react.css
import properties.AboutMeScreenProps
import react.FC
import react.dom.html.ReactHTML.div

val AboutMeScreen = FC<AboutMeScreenProps> { props ->
    div {
        AboutMeWhoIamSection {
            positioning = props.positioning
            myInfosAboutMe = props.myInfosAboutMe
            updatescreenState = props.updatescreenState
        }

        AboutMeWhatIDo {
            whatIDo = props.myInfosAboutMe.whatCanIDo
            updatescreenState = props.updatescreenState
        }

        SkillGrid {
            skills = props.mySkillsAboutMe
            positioning = props.positioning
            numColumns = props.numOfColumns
        }

        AboutMeTestimonials {
            positioning = props.positioning
            abutMeTestimonials = props.myTestimonialsAboutMe
        }
        css {
            display = Display.flex
            flexDirection = FlexDirection.column
            position = Position.sticky
            overflowY = Overflow.scroll
        }
    }

}