package components

import csstype.*
import emotion.react.css
import kotlinx.js.jso
import properties.TestimonyCardProps
import react.FC
import react.dom.html.ReactHTML
import react.useState
import states.SideBarState

val TestimonyCard = FC<TestimonyCardProps> { props ->
    var loadingImageState by useState<Boolean>(false)

    ReactHTML.div {
        ReactHTML.div {
            ReactHTML.img {
                src = if(!loadingImageState) "images/shimmer.gif" else props.testimpnyData.image
                onLoad = {
                    loadingImageState = true
                }
                css {
                    width = 100.px
                    height = 100.px
                    borderRadius = 50.pct
                }
            }
            ReactHTML.div {
                +props.testimpnyData.ownerName

                css {
                    fontSize = 20.px
                    fontWeight = FontWeight.bold
                    fontFamily = FontFamily.monospace
                    color = NamedColor.black
                    marginTop = 40.px
                    marginLeft = 20.px
                }
            }

            css {
                display = Display.flex
                justifyContent = JustifyContent.left
                marginBottom = 10.px
            }
        }

        ReactHTML.div {
            +props.testimpnyData.title
            css {
                fontSize = 14.px
                fontWeight = FontWeight.normal
                fontFamily = FontFamily.monospace
                color = Color("#07268cad")
                marginBottom = 10.px
            }
        }

        ReactHTML.div {
            +props.testimpnyData.testimony
            css {
                fontSize = 12.px
                fontWeight = FontWeight.normal
                fontFamily = FontFamily.monospace
                color = NamedColor.gray
                marginBottom = 10.px
            }
        }

        ReactHTML.div {
            ReactHTML.button {
                ReactHTML.a {
                    href = props.testimpnyData.facebook

                    ReactHTML.i {
                        className = ClassName("fa-brands fa-facebook")
                        style = jso {
                            fontSize = 20.px
                            color = Color("#54B689")
                            width = 100.pct
                            height = 100.pct
                            textAlign = TextAlign.center
                            verticalAlign = VerticalAlign.middle
                            position = Position.relative
                            marginTop = (5).px
                        }
                    }
                }

                css {
                    border = Border(width = 2.px, style = LineStyle.solid)
                    backgroundColor = NamedColor.white
                    borderColor = NamedColor.white
                    justifyContent = JustifyContent.center
                    borderRadius = 50.pct
                    width = 35.px
                    height = 35.px
                    alignItems = AlignItems.center
                    hover {
                        color = NamedColor.white
                        backgroundColor = Color("#c2c2c2")
                        borderColor = Color("#c2c2c2")
                    }
                }
            }

            ReactHTML.button {
                ReactHTML.a {
                    href = props.testimpnyData.linkedin

                    ReactHTML.i {
                        className = ClassName("fa-brands fa-linkedin-in")
                        style = jso {
                            fontSize = 20.px
                            color = Color("#54B689")
                            width = 100.pct
                            height = 100.pct
                            textAlign = TextAlign.center
                            verticalAlign = VerticalAlign.middle
                            position = Position.relative
                            marginTop = (5).px
                        }
                    }
                }

                css {
                    border = Border(width = 2.px, style = LineStyle.solid)
                    backgroundColor = NamedColor.white
                    borderColor = NamedColor.white
                    justifyContent = JustifyContent.center
                    borderRadius = 50.pct
                    width = 35.px
                    height = 35.px
                    alignItems = AlignItems.center
                    hover {
                        color = NamedColor.white
                        backgroundColor = Color("#c2c2c2")
                        borderColor = Color("#c2c2c2")
                    }
                }
            }

            ReactHTML.button {
                ReactHTML.a {
                    href = "mailto: ${props.testimpnyData.email}"

                    ReactHTML.i {
                        className = ClassName("fa-solid fa-envelope")
                        style = jso {
                            fontSize = 20.px
                            color = Color("#54B689")
                            width = 100.pct
                            height = 100.pct
                            textAlign = TextAlign.center
                            verticalAlign = VerticalAlign.middle
                            position = Position.relative
                            marginTop = (5).px
                        }
                    }
                }

                css {
                    border = Border(width = 2.px, style = LineStyle.solid)
                    backgroundColor = NamedColor.white
                    borderColor = NamedColor.white
                    justifyContent = JustifyContent.center
                    borderRadius = 50.pct
                    width = 35.px
                    height = 35.px
                    alignItems = AlignItems.center
                    hover {
                        color = NamedColor.white
                        backgroundColor = Color("#c2c2c2")
                        borderColor = Color("#c2c2c2")
                    }
                }
            }

            css {
                display = Display.flex
                justifyContent = JustifyContent.left
            }
        }

        css {
            width = if (props.positioning == SideBarState.SIDE) 80.pct else 100.pct
            marginBottom = 30.px
        }
    }

}