package components

import csstype.*
import emotion.react.css
import properties.SkillCardProperties
import react.FC
import react.dom.html.ReactHTML.div
import react.dom.html.ReactHTML.img
import states.SideBarState

val SkillCard = FC<SkillCardProperties> { props ->
    div {
        div {

            props.icon.forEach { icon ->
                img {
                    src = icon
                    css {
                        width = 40.px
                        height = 40.px
                    }
                }
            }
            css {
                display = Display.flex
                flexDirection = FlexDirection.row
                marginRight = 5.px
                marginBottom = 5.px
                width = 100.pct - 5.px
                backgroundColor = Color("#D4f5d2")
                borderTopLeftRadius = 10.px
                borderTopRightRadius = 10.px
                paddingLeft = 5.px
            }
        }
        div {
            +props.title
            css {
                fontSize = 18.px
                fontWeight = FontWeight.bold
                fontFamily = FontFamily.monospace
                marginBottom = 5.px
                paddingLeft = 5.px
                width = 100.pct
                width = if(props.positioning == SideBarState.SIDE) 300.px else 100.pct
            }
        }
        div {
            +props.info
            css {
                color = NamedColor.gray
                fontSize = 14.px
                fontWeight = FontWeight.normal
                fontFamily = FontFamily.monospace
                textAlign = TextAlign.justify
                paddingLeft = 5.px
                paddingRight = 5.px
                width = 100.pct - 10.px
                borderBottomLeftRadius = 10.px
                borderBottomRightRadius = 10.px
                backgroundColor = Color("#e9f5f9")
                height = 100.pct
                overflowY = Overflow.scroll
                scrollbarWidth = None.none
            }
        }
        css {
            display = Display.flex
            flexDirection = FlexDirection.column
            width = if(props.positioning == SideBarState.SIDE) 305.px else 100.pct
            height = 200.px
            backgroundColor = Color("#e9f5f9")
            borderRadius = 10.px
            marginRight = props.rightMargin
            marginBottom = props.bottomMargin
        }
    }

}