package data

import dev.gitlive.firebase.firestore.DocumentSnapshot

data class Experience(
    val title: String,
    val address: String,
    val company: String,
    val companyDescription: String,
    val image: String,
    val link: String,
    val period: String,
    val responsibility: String
) {
    companion object {
        fun fromDocument(myExperienceDocument: DocumentSnapshot): List<Experience> {
            val myExperience = myExperienceDocument.get<Map<String, Map<String, String>>>("experience")
            return myExperience.keys.map { key ->
                Experience(
                    title = key,
                    address = myExperience[key]!!["address"]!!,
                    company = myExperience[key]!!["company"]!!,
                    companyDescription = myExperience[key]!!["company_descriptions"]!!,
                    image = myExperience[key]!!["image"]!!,
                    link = myExperience[key]!!["link"]!!,
                    period = myExperience[key]!!["period"]!!,
                    responsibility = myExperience[key]!!["responsibility"]!!
                )
            }

        }
    }
}

var experienceList = listOf<Experience>()