package components

import csstype.*
import csstype.ScrollbarWidth.Companion.thin
import emotion.react.css
import kotlinx.js.jso
import react.FC
import react.dom.html.ReactHTML.button
import react.dom.html.ReactHTML.div
import react.dom.html.ReactHTML.i
import react.dom.html.ReactHTML.img
import react.useState
import screens.SCREENS
import states.RelativeSideBarState
import states.SideBarState

val ResponsiveSideBar = FC<RelativeSideBarState> { state ->
    var activeInd by useState<Int>(0)
    var loadingImageState by useState<Boolean>(false)

    if(state.positioning == SideBarState.TOP) {
        div {
            button {
                i {
                    className = ClassName("fa-solid fa-bars")
                    style = jso {
                        fontSize = 25.px
                        color = NamedColor.white
                    }
                }

                onClick = { state.updateShowRelativeState() }
                css {
                    margin=10.px
                    alignSelf = AlignSelf.center
                    width = 20.pct
                    border= Border(width = 2.px, style = LineStyle.dotted)
                    backgroundColor = Color("#54B689")
                    borderRadius = 5.px
                    borderColor = NamedColor.white
                    hover {
                        color = NamedColor.white
                        backgroundColor= Color("#555")
                    }
                }
            }
            div {
                +state.myInfos.name

                css {
                    width = 100.pct
                    textAlign = TextAlign.center
                    fontFamily = FontFamily.monospace
                    fontSize = 20.px
                    fontWeight = FontWeight.bold
                    color = Color("#fff")
                }
            }

            css {
                position = Position.fixed
                left = 0.px
                top = 0.px
                height = 10.vh
                width = 100.vw
                background = Color("#54B689")
                overflowY = Auto.auto
                //scrollbarColor= ScrollbarColor()
                scrollbarWidth = thin
                display = Display.flex
                alignItems = AlignItems.center
            }
        }


    }
    div {


        className = ClassName("force-overview")
        if (state.positioning == SideBarState.SIDE) {
            div {
                +state.myInfos.name

                css {
                    width = 100.pct
                    marginTop = 40.px
                    marginBottom = 20.px
                    textAlign = TextAlign.center
                    fontFamily = FontFamily.monospace
                    fontSize = 20.px
                    fontWeight = FontWeight.bold
                    color = Color("#fff")
                }
            }
        }

        div {

            img {

                src = if(!loadingImageState) "images/shimmer.gif" else state.myInfos.imageLink
                onLoad = {
                    loadingImageState = true
                }
                css {
                    borderRadius = 50.pct
                    width = 200.px
                    height = 200.px
                    alignSelf= AlignSelf.center
                }
            }

            css {
                width = 100.pct
                justifyContent = JustifyContent.center
                alignItems = AlignItems.center
                display = Display.flex
            }
        }

        div {
            +state.myInfos.hi

            css {
                width = 100.pct - 40.px
                padding = 20.px
                textAlign = TextAlign.center
                fontFamily = FontFamily.monospace
                fontSize = 14.px
                fontWeight = FontWeight.normal
                color = Color("#fff")
            }
        }

        ContactBar {}
        div {

            SideBarButton{
                title = "About Me"
                icon = "fa-solid fa-user-tie"
                screen = SCREENS.ABOUT_ME
                currentScreenState = state.currentScreenState
                onClick = {
                    println("Hello")
                    state.updatescreenState(SCREENS.ABOUT_ME)
                }
            }

            SideBarButton{
                title = "Portfolio"
                icon = "fa-solid fa-briefcase"
                screen = SCREENS.PORTFOLIO
                currentScreenState = state.currentScreenState
                onClick = {
                    state.updatescreenState(SCREENS.PORTFOLIO)
                }
            }

            SideBarButton{
                title = "Resume"
                icon = "fa-regular fa-file"
                screen = SCREENS.RESUME
                currentScreenState = state.currentScreenState
                onClick = {
                    state.updatescreenState(SCREENS.RESUME)
                }
            }

            SideBarButton{
                title = "Contact"
                icon = "fa-solid fa-address-book"
                screen = SCREENS.CONTACT
                currentScreenState = state.currentScreenState
                onClick = {
                    state.updatescreenState(SCREENS.CONTACT)
                }
            }
            css {
                display = Display.flex
                flexDirection = FlexDirection.column
                cursor = Cursor.pointer
                marginLeft = if(state.positioning == SideBarState.SIDE) 25.pct else 10.px
                marginTop = 15.px

            }
        }

        button {
            +"Hire Me"

            onClick = {
                state.updatescreenState(SCREENS.CONTACT)
            }
            css {
                width = 80.pct
                height = 40.px
                marginRight = 10.pct
                marginLeft = 10.pct
                marginTop = 20.px
                marginBottom = 30.px
                fontSize = 16.px
                fontWeight = FontWeight.bold
                fontFamily = FontFamily.monospace
                backgroundColor = Color("#07268cad")
                borderColor = Color("#07268cad")
                color = NamedColor.white
                borderRadius = 10.px
                hover {
                    backgroundColor = Color("#07268c")
                }

            }
        }


        css {
            position = if (state.positioning == SideBarState.SIDE) Position.fixed else Position.absolute
            left = 0.px
            top =  if (state.positioning == SideBarState.SIDE) 0.px else 10.vh
            height = if (state.positioning == SideBarState.SIDE) 100.vh else 40.vh
            width = if (state.positioning == SideBarState.SIDE) 300.px else 100.vw
            background = Color("#54B689")
            color = Color("#fff");
            overflowY = Auto.auto
            //scrollbarColor= ScrollbarColor()
            scrollbarWidth = thin
            display =
                if (state.positioning == SideBarState.SIDE) Display.block else if (state.showRelativeState) Display.block else None.none

        }

    }

}