package components

import csstype.*
import emotion.react.css
import kotlinx.js.jso
import properties.SideBarButtonProperties
import react.FC
import react.dom.html.ReactHTML.a
import react.dom.html.ReactHTML.div
import react.dom.html.ReactHTML.i
import react.dom.html.ReactHTML.li

val SideBarButton = FC<SideBarButtonProperties> { props ->
    li {
        a {
            i {
                className = ClassName(props.icon)
                style = jso {
                    fontSize = 15.px
                    color = if(props.screen == props.currentScreenState) Color("#0010a3") else NamedColor.white
                    textAlign = TextAlign.center
                    verticalAlign = VerticalAlign.middle
                    position = Position.relative
                    marginTop = (2.5).px
                    marginRight = 10.px
                }

            }
            div {
                +props.title
                css {
                    textAlign = TextAlign.start
                    fontFamily = FontFamily.monospace
                    fontSize = 17.px
                    fontWeight = FontWeight.bolder
                    color = if(props.screen == props.currentScreenState) Color("#292929") else NamedColor.white
                    hover {
                        color = Color("#0a3622")
                    }
                }
            }

            css {
                display = Display.flex
                verticalAlign = VerticalAlign.middle
                justifyContent = JustifyContent.center

            }
        }

        onClick = {
            println("pressed ${props.screen}")
            props.onClick()
        }

        css {
            display = Display.flex
            flexDirection = FlexDirection.row
            alignItems = AlignItems.center
            height = 40.px
        }
    }

}